  //     // 如果不允许进入全屏，发出不允许提示
  //     this.$message({
  //       message: "不支持全屏",
  //       type: "warning"
  //     });
  //     return false;
  //   }
  // screenfull.toggle();
  // // this.$message({
  // //     message: '全屏啦',
  // //     type: 'success'
  // //  })
  // },
  handleCollapse() {
    this.$emit("handleCollapse");
  }
}
